<template>
  <div style="padding:20px">

    <el-form :inline="true" @submit.native.prevent>
      <el-form-item label="">
        <el-input size="medium" v-model="queryObj.tag" placeholder="" clearable style="width:300px"
          @keyup.enter.native="onQuery"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button size="medium" type="primary" @click="onQuery"></el-button>
        <el-button size="medium" type="success" @click="newObject"></el-button>
      </el-form-item>
    </el-form>

      <el-table ref="singleTable" :data="dataList" highlight-current-row border style="width: 100%"
        v-loading="tableLoading" size="mini" :header-cell-style="{background:'#F3F4F7',color:'#555'}" row-key='id'>
        <el-table-column property="tag" label="tag" min-width="120" align='center'>
        </el-table-column>
        <el-table-column property="remark" label="" align="center" min-width="100">
        </el-table-column>
        <el-table-column property="executorNum" label="" align="center" width="150">
        </el-table-column>
        <el-table-column property="dateUpdated" label="" align="center" width="160" :formatter="dateFormat">
        </el-table-column>
        <el-table-column property="updatedBy" label="" align="center" width="160" >
        </el-table-column>

        <el-table-column property="operation" fixed="right" label="" align="center" width="150">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="editObject(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="deleteObject(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

    <el-row type="flex" justify="center" style="margin-top:15px;">
      <el-pagination @current-change="onQuery" :current-page.sync="queryObj.pageNum" :page-size="queryObj.pageSize"
        layout="total, prev, pager, next" :total="total">
      </el-pagination>
    </el-row>

    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="650px">

      <el-form label-position='right' label-width="110px">
        <el-form-item label="tag">
          <el-input v-model="currentObject.tag" :disabled="this.EditType==='edit'?true:false"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currentObject.remark"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="currentObject.executorNum"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </span>

    </el-dialog>

  </div>
</template>

<script>
import moment from 'moment'

export default {

  data () {
    return {
      tableLoading: false,
      dialogBatchShow: false,
      batchOperationFalg: false,

      queryObj: {
        tag: '',
        pageNum: 0,
        pageSize: 10

      },
      currentObject: {

      },
      dataList: [],
      total: 0,
      totalData: [],
      dialogVisible: false,
      dialogLoading: false,
      dialogTitle: '',
      EditType: 'new'

    }
  },
  mounted () {
    this.onQuery()
  },
  methods: {
    dateFormat: function (row, column) {
      var date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    dateFormatVal: function (date) {
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },

    onQuery () {
      this.tableLoading = true
      this.dataList = []
      this.$http.post('/api/tag/getAllTag', this.queryObj).then(res => {
        this.tableLoading = false
        this.dataList = res.list
        this.total = res.total
      })
    },
    newObject () {
      this.dialogTitle = 'tag'
      this.dialogVisible = true
      this.EditType = 'new'
      this.currentObject = {}
    },

    editObject (row) {
      this.dialogTitle = 'tag'
      this.dialogVisible = true
      this.EditType = 'edit'
      this.currentObject = JSON.parse(JSON.stringify(row))
    },
    save () {
      var url = ''
      if (this.EditType === 'new') {
        url = '/api/tag/'
        this.$http.post(url, this.currentObject).then(res => {
          this.dialogVisible = false
          this.$message.success('')
          this.onQuery()
        }).catch(errors => {
          this.$message.error(errors.msg)
        })
      } else {
        url = '/api/tag/'
        this.$http.put(url, this.currentObject).then(res => {
          this.dialogVisible = false
          this.$message.success('')
          this.onQuery()
        }).catch(errors => {
          this.$message.error(errors.msg)
        })
      }
    },
    deleteObject (row) {
      this.$http.delete('/api/tag/' + row.tag).then(res => {
        this.dialogVisible = false
        this.$message.success('')
        this.onQuery()
      }).catch(errors => {
        this.$message.error(errors.msg)
      })
    }
  }

}
</script>

<style scoped>
.tableHeader {
  background-color: red !important;
}
</style>
