/* eslint-disable */
import axios from 'axios'
const instance = axios.create({
  timeout: 15000,
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
    'Content-Type': 'application/json; charset=UTF-8',
    'Access-Control-Allow-Origin': '*',
    xsrfCookieName: 'XSRF-TOKEN',
    dataType: 'json'
  }
})
instance.interceptors.response.use(
  response => {
    if (response.data && response.data.flag === "S") {
      let res = response.data.data || []
      if (res instanceof Object) {
        res.__successMsg = response.data.msg // 
        res.__code = response.data.code // 
        res.__flag = response.data.flag // 
      } else {
        // 


      }
      return Promise.resolve(res)
    }
    return Promise.reject({ msg: response.data.msg, code: response.data.code, data: response.data.data })
  },
  e => {
    return Promise.reject({ message: ',!' })
  }
)
export default instance
