<template>
  <div style="padding:20px">

    <el-form :inline="true" @submit.native.prevent>
      <el-form-item label="">
        <el-select v-model="queryObj.dealSysCode" placeholder="" clearable filterable>
          <el-option v-for="item in allSysCodes" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="medium" v-model="queryObj.taskName" placeholder="" clearable style="width:200px"
          @keyup.enter.native="onQuery"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="queryObj.state" placeholder="" clearable>
          <el-option label="" value="normal"></el-option>
          <el-option label="" value="pause"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="medium" type="primary" @click="onQuery"></el-button>
        <el-button size="medium" type="success" @click="newObject"></el-button>
      </el-form-item>
    </el-form>

    <el-table ref="singleTable" :data="dataList" highlight-current-row border style="width: 100%"
      v-loading="tableLoading" size="mini" @row-dblclick="onShow" :header-cell-style="{background:'#F3F4F7',color:'#555'}"
      row-key='id'>
      <el-table-column property="tag" label="tag" align="center" width="110"></el-table-column>
      <el-table-column property="taskName" label="" min-width="140" align='center'> </el-table-column>
      <el-table-column property="remark" label="" align="center" min-width="140"> </el-table-column>
      <el-table-column property="assignNum" label="" align="center" width="90"> </el-table-column>
      <el-table-column property="threadNumber" label="" align="center" width="60"> </el-table-column>
      <el-table-column property="permitRunStartTime" label="" align="center" width="150"> </el-table-column>
      <el-table-column property="state" label="" align="center" width="90">
        <template slot-scope="scope">
            <el-tag v-if='scope.row.state==="normal"' type="success" size="mini" @click.stop="runtimeInfo(scope.row)"> </el-tag>
            <el-tag v-else  size="mini"> </el-tag>
            <i v-if="taskChangeStateMap[scope.row.id]" class="el-icon-loading"></i>
            <el-tag size="mini" v-if="taskChangeResultMap[scope.row.id+'succCount'] > 0" type="success">{{taskChangeResultMap[scope.row.id+'succCount']}}</el-tag>
            <el-tag size="mini" v-if="taskChangeResultMap[scope.row.id+'failCount'] > 0" type="danger">{{taskChangeResultMap[scope.row.id+'failCount']}}</el-tag>
         </template>
      </el-table-column>
      <el-table-column property="dateUpdated" label="" align="center" width="150" :formatter="dateFormat">      </el-table-column>
      <el-table-column property="updatedBy" label="" align="center" width="80"> </el-table-column>

      <el-table-column property="operation" fixed="right" label="" align="center" width="300">
        <template slot-scope="scope">
          <el-button v-if="scope.row.state==='pause'" plain size="mini" type="info" @click.stop="changeStatus(scope.row,'normal')"></el-button>
          <el-button v-if="scope.row.state==='normal'" plain size="mini" type="warning" @click.stop="changeStatus(scope.row,'pause')"></el-button>
          <el-button size="mini" type="primary" @click.stop="editObject(scope.row)"></el-button>
          <el-button size="mini" type="warning" @click.stop="copyObject(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click.stop="deleteObject(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-row type="flex" justify="center" style="margin-top:15px;">
      <el-pagination background layout="total, prev, pager, next, jumper" :current-page.sync="queryObj.pageNum"
        @current-change="onQuery" :total="total" :page-size="queryObj.pageSize">
      </el-pagination>
    </el-row>

    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="1280px">
      <el-form label-position='right' label-width="170px" size="mini" :model="detailObject" ref='objForm' >
        <el-row>
          <el-col :span="8">
            <el-form-item label="" prop="cluster" :rules="rule">
              <el-select v-model="detailObject.cluster" placeholder="" :disabled="this.EditType=='edit'" >
                <el-option v-for="item in clusterList" :key="item.cluster" :label="item.cluster" :value="item.cluster">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="dealSysCode" :rules="rule">
              <el-select v-model="detailObject.dealSysCode" placeholder="">
                <el-option v-for="item in allSysCodes" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="tag" prop="tag" :rules="rule">
              <el-select v-model="detailObject.tag" placeholder="">
                <el-option v-for="item in allTagCodes" :key="item" :label="item" :value="item" >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>

        </el-row>
        <el-row>
         <el-col :span="8">
            <el-form-item label="BeanName" prop="dealBeanName" :rules="rule">
              <el-select v-model="detailObject.dealBeanName" >
                <el-option label="DUBBO" value="DUBBO"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="processorType" :rules="rule">
              <el-select v-model="detailObject.processorType" placeholder="">
                <el-option label="SLEEP" value="SLEEP"></el-option>
                <el-option label="SIMPLE" value="SIMPLE"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-select v-model="detailObject.forceCronExec">
                <el-option label="Y()" value="Y"></el-option>
                <el-option label="N()" value="N"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
           <el-col :span="12">
            <el-form-item label="" prop="taskName" :rules="rule">
              <el-input v-model="detailObject.taskName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Bean" prop="dealBizBeanName" :rules="rule">
              <el-input v-model="detailObject.dealBizBeanName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="permitRunStartTime" :rules="rule">
              <el-input v-model="detailObject.permitRunStartTime"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.permitRunEndTime"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.sleepTimeNoData"></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.sleepTimeInterval"></el-input>
            </el-form-item>
          </el-col>
            </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.fetchDataNumber"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.executeNumber"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.assignNum"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.threadNumber"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.remark"></el-input>
            </el-form-item>
          </el-col>
           <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="detailObject.taskItems"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="">
          <el-input v-model="detailObject.taskParameter"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="validateObj"> </el-button>
      </span>

    </el-dialog>

    <el-dialog :title="runtimeDialogTitle" :visible.sync="runtimeDialogVisible" width="1700px">
      <el-table ref="singleTable" :data="runtimeList" highlight-current-row border style="width: 100%" v-loading="tableLoading" size="mini" :header-cell-style="{background:'#F3F4F7',color:'#555'}" row-key='id'>
        <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.message }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column property="seqNo" label="" align="center" width="50"> </el-table-column>
      <el-table-column property="registerTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="hostName" label="" align="center" min-width="100"> </el-table-column>
      <el-table-column property="address" label="" align="center" width="150"> </el-table-column>
      <el-table-column property="heartBeatTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="state" label="" align="center" width="70">
        <template slot-scope="scope">
            <el-tag v-if='scope.row.state==="init"'  size="mini"></el-tag>
            <el-tag v-if='scope.row.state==="normal"' type="success" size="mini"></el-tag>
            <el-tag v-if='scope.row.state==="pause"'  size="mini"></el-tag>
            <el-tag v-if='scope.row.state==="error"'  size="mini"></el-tag>
            <el-tag v-if='scope.row.state==="dead"'  size="mini"></el-tag>
         </template>
      </el-table-column>
      <el-table-column property="lastFetchDataTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="nextRunStartTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="nextRunEndTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="taskItems" label="" align="center"> </el-table-column>
    </el-table>
    </el-dialog>

    <el-drawer :title="'['+currentObject.taskName+']'" :visible.sync="detailShow" direction="ttb" class="expandDrawer" size='300'>
      <el-form :inline="true" style='padding:0px 30px'>
        <el-row>
           <el-col :span="6">
            <el-form-item label="Bean">
              {{currentObject.dealBizBeanName}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.processorType}}
            </el-form-item>
          </el-col>
           <el-col :span="6">
            <el-form-item label="Bean">
              {{currentObject.dealBeanName}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="(s)">
              {{currentObject.heartBeatRate}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.permitRunStartTime}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.permitRunEndTime}}
            </el-form-item>
          </el-col>

          <el-col :span="6">
            <el-form-item label="/">
              {{currentObject.fetchDataNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="/">
              {{currentObject.executeNumber}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.threadNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.threadNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="(s)">
              {{currentObject.sleepTimeNoData}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="(s)">
              {{currentObject.judgeDeadInterval}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.taskItems}}
            </el-form-item>
          </el-col>
          <el-col :span="18">
            <el-form-item label="">
              {{currentObject.taskParameter}}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-drawer>

  </div>
</template>

<script>
import moment from 'moment'

export default {

  data () {
    return {
      tableLoading: false,
      runtimeTableLoading: false,
      dialogBatchShow: false,
      batchOperationFalg: false,
      detailShow: false,
      dialogTitle: '',
      runtimeDialogTitle: '',
      dialogVisible: false,
      runtimeDialogVisible: false,
      EditType: 'new',
      queryObj: {
        state: '',
        cluster: '',
        pageNum: 0,
        pageSize: 10
      },
      detailObject: {
        cluster: '',
        dealSysCode: '',
        tag: '',
        dealBeanName: '',
        processorType: '',
        forceCronExec: '',
        taskName: '',
        dealBizBeanName: '',
        permitRunStartTime: '',
        permitRunEndTime: '',
        sleepTimeNoData: '',
        sleepTimeInterval: '',
        fetchDataNumber: '',
        executeNumber: '',
        assignNum: '',
        threadNumber: '',
        remark: '',
        taskItems: '',
        taskParameter: ''
      },
      currentObject: {},
      runtimeList: [],
      dataList: [],
      total: 0,
      totalData: [],
      allSysCodes: [],
      allTagCodes: [],
      taskChangeStateMap: {},
      taskChangeResultMap: {},
      clusterList: [],
      rule: [
        { required: true, message: '', trigger: 'blur,change' }
      ]
    }
  },
  mounted () {
    this.loadAllSysCodes()
    this.onQuery()
    this.loadAllCluster()
    this.loadAllTagCodes()
  },
  methods: {
    dateFormat: function (row, column) {
      var date = row[column.property]
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    dateFormatVal: function (date) {
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    loadAllSysCodes () {
      this.$http.get('/api/system/loadAllSysCodes').then(res => {
        this.allSysCodes = res
      })
    },
    loadAllTagCodes () {
      this.$http.get('/api/tag/loadAllTagCodes').then(res => {
        this.allTagCodes = res
      })
    },
    onQuery () {
      this.tableLoading = true
      this.queryObj.cluster = this.$store.state.cluster
      this.$http.post('/api/task/getAll', this.queryObj).then(res => {
        this.tableLoading = false
        this.dataList = res.list
        var tmpChangeStateMap = {}
        var tmpChangeResultMap = {}
        for (var i = 0; i < this.dataList.length; i++) {
          tmpChangeStateMap[this.dataList[i].id] = false
          tmpChangeResultMap[this.dataList[i].id + 'succCount'] = 0
          tmpChangeResultMap[this.dataList[i].id + 'failCount'] = 0
        }
        this.taskChangeStateMap = tmpChangeStateMap
        this.taskChangeResultMap = tmpChangeResultMap
        this.total = res.total
      }).catch(errors => {
        this.dataList = []
        this.$message.error(errors.msg)
        this.tableLoading = false
      })
    },
    newObject () {
      this.detailObject = {}
      this.dialogTitle = ''
      this.dialogVisible = true
      this.EditType = 'new'
    },
    editObject (row) {
      this.dialogTitle = ''
      this.dialogVisible = true
      this.EditType = 'edit'
      this.detailObject = JSON.parse(JSON.stringify(row))
    },
    copyObject (row) {
      this.dialogTitle = '()'
      this.dialogVisible = true
      this.EditType = 'copy'
      this.detailObject = JSON.parse(JSON.stringify(row))
    },
    runtimeInfo (row) {
      let data = {}
      data.env = row.env
      data.cluster = row.cluster
      data.taskName = row.taskName
      this.runtimeTableLoading = true
      this.runtimeList = []
      this.$http.post('/api/task/taskRuntime', data).then(res => {
        this.runtimeList = res
        this.runtimeTableLoading = true
      }).catch(errors => {
        this.$message.error(errors.msg)
        this.runtimeTableLoading = false
      })
      this.runtimeDialogTitle = '[' + row.taskName + ']'
      this.runtimeDialogVisible = true
    },
    validateObj () {
      debugger
      this.$refs['objForm'].validate((valid) => {
        if (valid) {
          this.save()
        } else {
          this.$message({
            message: '',
            type: 'error'
          })

          return false
        }
      })
    },
    save () {
      var url = ''
      if (this.EditType === 'new' || this.EditType === 'copy') {
        url = '/api/task/'
        this.$http.post(url, this.detailObject).then(res => {
          this.dialogVisible = false
          this.$message.success('')
          this.onQuery()
        }).catch(errors => {
          this.$message.error(errors.msg)
        })
      } else {
        url = '/api/task/'
        this.$http.put(url, this.detailObject).then(res => {
          this.dialogVisible = false
          this.$message.success('')
          this.onQuery()
        }).catch(errors => {
          this.$message.error(errors.msg)
        })
      }
    },
    deleteObject (row) {
      if (row.state === 'normal') {
        this.dialogVisible = false
        this.$message({
          message: '',
          type: 'error'
        })
        return
      }
      this.$http.delete('/api/task/' + row.taskName).then(res => {
        this.dialogVisible = false
        this.$message({
          message: '',
          type: 'success'
        })
        this.onQuery()
      }).catch(errors => {
        this.$message.error(errors.msg)
      })
    },
    onShow (row) {
      this.currentObject = row
      this.detailShow = true
    },
    loadAllCluster () {
      this.$http.get('/api/cluster/getAllCluster').then(res => {
        this.clusterList = res
      })
    },
    changeStatus (row, state) {
      row.state = state
      this.$http.put('/api/task/', row).then(res => {
        this.dialogVisible = false
        this.$message.success('')
        this.refreshRuntimeState(row, state)
      }).catch(errors => {
        this.$message.error(errors.msg)
      })
    },
    refreshRuntimeState (row, state) {
      let data = {}
      data.cluster = row.cluster
      data.taskName = row.taskName
      let assignNum = row.assignNum
      let id = row.id
      let _this = this
      _this.taskChangeStateMap[id] = true
      _this.taskChangeResultMap[id + 'succCount'] = 0
      _this.taskChangeResultMap[id + 'failCount'] = 0
      var time = setInterval(() => {
        _this.$http.post('/api/task/taskRuntime', data).then(res => {
          if ((state === 'pause' && res.length == 0) || (state === 'normal' && assignNum == res.length)) {
            let failCount = 0
            let succCount = 0
            for (var i = 0; i < res.length; i++) {
              if (res[i].state === 'error') {
                failCount = failCount + 1
              } else {
                succCount = succCount + 1
              }
            }
            _this.taskChangeResultMap[id + 'succCount'] = succCount
            _this.taskChangeResultMap[id + 'failCount'] = failCount
            _this.taskChangeStateMap[id] = false
            window.clearInterval(time)
          }
          _this.runtimeList = res
        }).catch(errors => {
          _this.taskChangeStateMap[id] = false
          window.clearInterval(time)
        })
      }, 3000)
      // $oncebeforeDestroy
      this.$once('hook:beforeDestroy', () => {
        clearInterval(time)
      })
    }
  },
  watch: {
    '$store.state.cluster': function (val, oldVal) {
      if (val) {
        this.onQuery()
      }
    }
  }

}
</script>

<style scoped>
.tableHeader {
  background-color: red !important;
}
.expandDrawer .el-form-item{
  margin-bottom: 0px;
  height: 40px;
  width: 100%;
}
</style>
