
import Vuex from 'vuex'
import axios from 'axios'
import Vue from 'vue'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    status: '',
    cluster: localStorage.getItem('cluster') || 'default',
    token: localStorage.getItem('token') || ''
  },
  mutations: {
    cluster_change (state, cluster) {
      state.cluster = cluster
    },
    auth_request (state) {
      state.status = 'loading'
    },
    auth_success (state, token) {
      state.status = 'success'

      state.token = token
    },
    auth_error (state) {
      state.status = 'error'
    },
    logout (state) {
      state.status = ''
      state.token = ''
    }
  },
  actions: {
    Login ({commit}, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request')
        // tokencommitstorestatetokenlocalStorage
        axios.post('/api/login', user)
          .then(resp => {
            if (resp.data.flag === 'S') {
              resolve(resp.data.data)
            } else {
              reject(resp.data.msg)
            }
            localStorage.setItem('token', resp.data.data)
            // headersToken
            axios.defaults.headers.common['Authorization'] = resp.data.data
            // token
            commit('auth_success', resp.data.data)
          })
          .catch(err => {
            commit('auth_error')
            localStorage.removeItem('token')
            reject(err)
          })
      })
    },
    LogOut ({ commit }) {
      return new Promise(resolve => {
        localStorage.removeItem('token')
        commit('logout')
        resolve()
      })
    },
    ClusterChange ({ commit }, cluster) {
      return new Promise(resolve => {
        localStorage.removeItem('cluster')
        commit('cluster_change', cluster)
      })
    }
  },
  getters: {
    // !!state.tokenstate.token()truefalse
    isLoggedIn: state => !!state.token,
    authStatus: state => state.status
  }
})

export default store
